import React from 'react';
import ContentBox from 'components/common/ContentBox';
import {
    Typography,
    Link
} from '@mui/material'

const HomePage = () => {
    return (
        <ContentBox>
            <Typography variant='h4' gutterBottom>
                Nikolay Vereshchagin Repository
            </Typography>
            <Typography paragraph>
                     <Link href='https://github.com/Nikololoshka'>Nikololoshka</Link>.
                  (    )   - Nikolay Vereshchagin.
                         .  !
            </Typography>
            <Typography variant='h5' gutterBottom>
                
            </Typography>
            <Typography paragraph>
                    " ".
                    4 .
                       Android.
                    SOLID,   
                MVVM, MVC, MVP.  ,  , .
            </Typography>
            <Typography paragraph>
                   , :
            </Typography>

            <ul>
                {'Java  Kotlin (Android); Python (Jupyter notebook, Numpy, Pandos, OpenCV); C++ (Qt  QML); TypeScript (React)'
                    .split('; ').map((text) =>
                        <li key={text}>
                            {text}
                        </li>
                    )
                }
            </ul>

            <Typography paragraph>
                   :
            </Typography>

            <ul>
                {('Kotlin + Java; Coroutines; Android SDK; Android Jetpack; Jetpack Compose; MVVM; Retrofit2 + OkHttp3; ' +
                    'Firebase (Storage, Analytics, Crashlytics); Dagger2; Glide'
                ).split('; ').map((text) =>
                    <li key={text}>
                        {text}
                    </li>
                )}
            </ul>

        </ContentBox>
    );
}

export default HomePage;